<script>
import JsonViewer
    from 'vue-json-viewer'
import http
    from '../../cloud/http'
import Sign
    from '../../utils/sign.js'


export default {
    name: 'Api',
    components: {
        JsonViewer,
    },
    data() {
        return {
            isLoading: false,
            service: this.$route.query.service || '',
            serviceName: '',
            defaultActive: '1-1',
            modules: [],
            api: {
                params: []
            },
            debugModule: '',
            responseJsonData: "",
            requestJsonData: "",
            activeResultTab: 'request',
            activeParamsTab: 'biz',
            openeds: [],
            requestHeader: {},
            project: {
                debug_appid: '',
                debug_appkey: '',
                debug_mpappid: '',
                debug_params: [
                    {
                        key: 'appid',
                        value: '{appid}',
                        desc: '请求商户APPID'
                    },
                    {
                        key: 'mpappid',
                        value: '{mpappid}',
                        desc: '小程序appid,非小程序端可不传'
                    },
                    {
                        key: 'rand',
                        value: '{rand}',
                        desc: '随机字符串'
                    },
                    {
                        key: 'timestamp',
                        value: '{timestamp}',
                        desc: '请求时间戳'
                    },
                    {
                        key: 'sign',
                        value: '{sign}',
                        desc: '签名',
                    },
                    {
                        key: 'token',
                        value: this.$cookie.get('ws_debug_token') || '',
                        desc: '用户令牌'
                    },
                    {
                        key: 'sessionkey',
                        value: this.$cookie.get('ws_debug_sessionkey') || '',
                        desc: '小程序登录sessionkey'
                    },
                    {
                        key: 'client',
                        value: this.$cookie.get('ws_debug_client') || 'debug',
                        desc: '客户端标识[ios;xiaomi;...]'
                    },
                    {
                        key: 'version',
                        value: this.$cookie.get('ws_debug_version') || '1.0.1',
                        desc: '客户端版本号'
                    }
                ]
            },
            app: []
        }
    },
    watch: {
        service() {
            if (!this.service) {
                return
            }
            this.getServiceName()
            //this.getDocument()
        }
    },
    mounted() {
        this.load()
    },
    methods: {
        load() {
            this.$loading.show();
            this.$request.get('/ws/document?action=init&mode=rpc').then(result => {
                if (result.errCode) {
                    return this.$message.error(result.message)
                }
                console.log(result.data)
                this.project.debug_appid = result.data.debug_appid
                this.project.debug_appkey = result.data.debug_appkey
                this.project.debug_mpappid = result.data.debug_mpappid
                this.app = result.data.modules
                this.gateway = result.data.gate_way

                if (this.app.length) {
                    this.service = this.service || this.app[0].name
                    this.getDocument()
                }
            }).catch(error => {
                this.$message.error(error)
            }).finally(() => {
                this.$loading.close();
            })
        },
        //获取文档
        getDocument() {
            if (!this.service) {
                return this.$message.error('请传入模块参数');
            }
            this.isLoading = true
            this.$request.get(`/ws/document?action=document&module=${this.service}&mode=rpc`).then(result => {
                if (result.errCode) {
                    return this.$message.alert(result.message)
                }
                this.modules = result.data
                if (this.modules.length) {
                    this.detail(this.modules[0].methods[0], 0)
                }
            }).catch(error => {
                this.$message.error(error)
            }).finally(() => {
                this.isLoading = false
            })
        },
        getServiceName() {
            this.app.forEach(item => {
                if (item.name === this.service) {
                    this.serviceName = item.cn_name || item.name
                }
            })
        },
        selectModule() {
            this.getDocument()
        },
        beforeUpload() {

        },
        onChange() {

        },
        onProgress() {
            this.$loading.show('上传中')
        },
        onSuccess(result) {
            this.$loading.close()
            if (result.errCode) {
                return this.$message.alert('文件上传失败:' + result.message)
            }
            this.$message.success('文件上传成功')
            this.responseJsonData = result
        },
        goBack() {

        },
        debug() {
            let args = [];
            if (this.api.params.length) {
                this.$copy(this.api.params).forEach(v => {
                    args.push(v.value)
                })
            }
            this.isLoading = true
            let data = {
                service: this.debugModule.name,
                method: this.api.name,
                args: args
            }
            http({
                url: '/admin/api/rpc/debug/',
                method: 'post',
                headers: this.requestHeader,
                data
            }).then(result => {
                this.responseJsonData = result
            }).catch(error => {
                this.$message.error(error)
            }).finally(() => {
                this.isLoading = false
            })
        },

        detail(api, moduleKey) {
            this.api = this.$copy(api)
            this.debugModule = this.modules[moduleKey]
            this.api.updated = this.debugModule.updated
            this.api.author = this.debugModule.author
            this.requestJsonData = null
            this.responseJsonData = null
            if (this.api.params) {
                let params = this.api.params
                params.forEach((item, key) => {
                    this.$set(this.api.params[key], 'value', '')
                    if (item.type === 'array') {
                        this.$set(this.api.params[key], 'value', ['', ''])
                    }
                })
            }
            this.$nextTick(() => {
                this.scrollTop(document.querySelector(".el-main").scrollTop)
            });
        },
        scrollTop(top) {
            setTimeout(() => {
                document.querySelector(".el-main").scrollTop = top;
                top = top - 20
                if (top > 0) {
                    this.scrollTop(top)
                } else if (top < 0) {
                    document.querySelector(".el-main").scrollTop = 0;
                }
            }, 1)
        },
        setIndex(params) {
            params.forEach(item => {
                item._index = Sign.rand()
                if (item.children) {
                    this.setIndex(item.children)
                }
            })
        }
    },
}
</script>
